<template>
  <div class="login-container">
    <!-- <header>
      <span class="appName">运营管理后台</span>
    </header> -->

    <div class="login-box">
      <p class="appName">运营管理后台</p>
      <p class="login-tit">登陆</p>
      <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="0" class="demo-ruleForm">
        <el-form-item label="" prop="account">
          <el-input
            placeholder="请输入账号"
            prefix-icon="icon-user iconfont"
            v-model="ruleForm.account"
            auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input
            placeholder="请输入密码"
            prefix-icon="icon-lock iconfont"
            v-model="ruleForm.password"
            type="password"
            auto-complete="off"></el-input>
        </el-form-item>
        <el-button type="primary" class="login-btn" native-type='submit' @click.native.prevent='submitForm'>登陆</el-button>
      </el-form>
    </div>
  </div>
</template>

<script>
import validate from '@/assets/js/validate';
import api from '@/api';
import store from '@/store/index';
import { mapGetters,mapActions } from "vuex";
import qs from 'qs';
import config from '@/api/config.js'
import cookie from '@/assets/js/cookie.js'
import { setTimeout } from 'timers';

export default {
  data() {
    return {
      ruleForm: {
        account: '',
        password: '',
      },
      rules: {
        account: [{ validator: validate.checkLoginUser, trigger: 'blur' }],
        password: [{ validator: validate.checkLoginPass, trigger: 'blur' }],
      },
    };
  },
  beforeRouteEnter(to, from, next) {
    store.dispatch('checkLogin', {
      success: () => {
        next({
          path: from.path,
        });
      },
      error: () => {
        next();
      },
    });
  },
  created() {

  },
  mounted() {

  },
  methods: {
    submitForm() {
      const that = this;
      this.$refs['ruleForm'].validate((valid) => {
        if (valid) {
          that.login();
        } else {
          console.log('error submit!!');
        }
      });
    },
    login() {
      const me = this;
      me.loading = true;
      let clientId = config.global[config.env]['client_id'];
      me.$store.dispatch('doLogin', {
        params: {
          account: me.ruleForm.account,
          password: me.ruleForm.password,
          client_id: clientId,
          url: window.location.protocol + '//' + window.location.host 
        },
        success: (data) => {
          me.$message.success('登录成功');
          setTimeout(()=> {
            me.$router.replace({
              path: me.$route.query.rurl ? me.$route.query.rurl : '/'
            });
          }, 500)
        },
        error: (e) => {
          me.$message.error(e);
          me.loading = false;
        },
      });
    }
  },
  computed: {
    ...mapGetters(['getPermissionData'])
  }
};
</script>

<style scoped lang="scss">
.login-container{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  background: 
  radial-gradient(rgba(255,255,255,0) 0, rgba(255,255,255,.15) 30%, rgba(255,255,255,.3) 32%, rgba(255,255,255,0) 33%) 0 0,
  radial-gradient(rgba(255,255,255,0) 0, rgba(255,255,255,.1) 11%, rgba(255,255,255,.3) 13%, rgba(255,255,255,0) 14%) 0 0,
  radial-gradient(rgba(255,255,255,0) 0, rgba(255,255,255,.2) 17%, rgba(255,255,255,.43) 19%, rgba(255,255,255,0) 20%) 0 110px,
  radial-gradient(rgba(255,255,255,0) 0, rgba(255,255,255,.2) 11%, rgba(255,255,255,.4) 13%, rgba(255,255,255,0) 14%) -130px -170px,
  radial-gradient(rgba(255,255,255,0) 0, rgba(255,255,255,.2) 11%, rgba(255,255,255,.4) 13%, rgba(255,255,255,0) 14%) 130px 370px,
  radial-gradient(rgba(255,255,255,0) 0, rgba(255,255,255,.1) 11%, rgba(255,255,255,.2) 13%, rgba(255,255,255,0) 14%) 0 0,
  linear-gradient(45deg, #343702 0%, #184500 20%, #187546 30%, #006782 40%, #0b1284 50%, #760ea1 60%, #83096e 70%, #840b2a 80%, #b13e12 90%, #e27412 100%);
  background-size: 470px 470px, 970px 970px, 410px 410px, 610px 610px, 530px 530px, 730px 730px, 100% 100%;
  background-color: #840b2a;
  header {
    display: block;
    height: 100px;
    line-height: 100px;
    // box-shadow: 0 2px 20px 0 rgba(0, 0, 0, 0.1);
    text-align: left;
    .appName {
      padding-left: 50px;
      font-size: 30px;
      color: #fff;
    }
  }

    .login-box{
      position: absolute;
      width: 320px;
      margin-left: -190px;
      left: 50%;
      top: 30%;
      background-color: #fff;
      padding: 20px 30px;
      border-radius: 10px;
      .appName{
        font-size: 22px;
        text-align: center;
        border-bottom: 1px solid #eee;
        padding-bottom: 14px;
        margin-bottom: 20px;
      }
      .login-tit{
        font-size: 16px;
        margin-bottom: 20px;
        text-align: left;
      }
      .login-btn{
        width: 100%;
      }
    }
  
}

</style>



